
.page {
  height: 100%;
  position: relative;
}
.header {
  background-color: #fff;
  position: absolute;
  width: 100%;
  top: 0px;
  height: 48px;
  line-height: 48px;
  border-bottom: 1px solid #e0e0e0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.left, .right {
  width: 40px;
  padding: 0 10px;
  cursor: pointer;
}
.center {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.content {
  padding: 0 10px;
  position: absolute;
  box-sizing: border-box;
  width: 100%;
  top: 49px;
  bottom: 0px;
  overflow: auto;
}



