
html, body, #app {
  height: 100%;
  margin: 0;
}
p {
  margin: .5em 0;
}
a {
  line-height: 40px;
}
#app {
  margin: 0 auto;
  background-color: #f0f0f0;
  max-width: 414px;
  height: 100%;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s
}
.fade-enter, .fade-leave-active {
  opacity: 0
}



